import db from '../services/firebase';
import { onSnapshot, collection } from '@firebase/firestore';

import { apiEndPoints, fetchApi } from '../services/api';
import { normalizePrice } from '../services/helpers';

const getNotificationMessage = async () => {
  const signedInUser: string | null = localStorage.getItem('userId');
  let notificationMessage: string = '';

  if (signedInUser) {
    onSnapshot(collection(db, 'users'), async (snapshot: any) => {
      const db = snapshot.docs.map((doc: any) => doc.data());
      const data = db.find((data: any) => data.userId === signedInUser);
      if (data) {
        const notificationCoins = data.notification.map(
          (coin: any) => coin.slug,
        );
        if (notificationCoins.length > 0) {
          const requestURL = `${
            apiEndPoints.coinsQuotes
          }${notificationCoins.join(',')}`;
          const response = await fetchApi(requestURL);
          const json = await response.json();
          const latestQuotes = Object.values(json.data).map((coin: any) => {
            return {
              name: coin.name,
              price: `$${normalizePrice(coin.quote.USD.price)}`,
            };
          });
          const notificationStatus = latestQuotes.map((coin: any) => {
            return `${coin.name} is currently at ${coin.price}.`;
          });
          notificationMessage = notificationStatus.join('\n');
        }
      }
    });
  }
  return notificationMessage;
};

const notificationInBackground = () => {
  //Setting up alarm for notification.
  chrome.alarms.create('notificationAlarm', {
    periodInMinutes: 1,
  });

  //Firing notification upon that alarm.
  chrome.alarms.onAlarm.addListener(async (alarm) => {
    if (alarm.name === 'notificationAlarm') {
      const message = await getNotificationMessage();
      chrome.notifications.create('price change', {
        type: 'basic',
        iconUrl: 'img/icon-128.png',
        title: 'Price Change Notification',
        message: message,
        priority: 2,
      });
    }
  });
};

export default notificationInBackground();
